import {
	mapMutations,
	mapState
} from 'vuex';
export default {
	data() {
		return {
			payType: 1,
			orderInfo: {}
		};
	},
	computed: {
		...mapState(['fee', 'amount', 'orderItemList', 'userInfo'])
	},
	onLoad(options) {

	},

	methods: {
		//选择支付方式
		changePayType(type) {
			this.payType = type;
		},
		//确认支付
		confirm: async function() {
			var sysOrder = {};
			sysOrder.clientId = this.userInfo.clientId;
			sysOrder.amount = this.amount;
			sysOrder.fee = this.fee;
			uni.request({
				url: this.constVal.baseURL + '/system/order',
				method: 'POST',
				data: {
					sysOrder: sysOrder,
					orderItemList: this.orderItemList,
				},
				success: (res) => {
					console.log(res.data);
				}
			})
			uni.redirectTo({
				url: '/pages/money/paySuccess'
			})
		},
	}
}
